            //本周/上周/本月/上月
            thisWeek(type){
                this.weekData = [];
                this.weekData = this.returnWeek(type);
                this.$store.state.weekData = this.returnWeek(type);
                if(type === 'thisweek'){
                    this.$store.state.weekText = 1;
                }else if(type === 'lastweek'){
                    this.$store.state.weekText = 2;
                }else if(type === 'thismonth'){
                    this.$store.state.weekText = 3;
                }else if(type === 'lastmonth'){
                    this.$store.state.weekText = 4;
                }else{
                    this.$store.state.weekText = 1;
                }
            },

