            //获取模块
            getTemplate() {
                this.getAjax('/sys/gltype/getTemplate')
                    .then(result => {
                        if(JSON.stringify(result.data) == "{}" || result.data.length === 0 ) {
                            this.getSomeData();
                            // this.$message({
                            //     message:result.message,
                            //     type: 'error'
                            // });
                        }else {
                            this.items = result.data[0].dicList;
                            this.position = result.data[0].roleidname;
                        }
                    })
            },

            //获取数据字典
            getSomeData() {
                this.postDicData([{'nodekey':'tygrzymk'}]).then( (result)=>{
                    if (result.status === 200) {
                        this.items = result.data['tygrzymk'];
